<template>
	<div id="search">
		<search-header>
			<span class="search-btn" slot="icon" @click="searchClick">搜素</span>
		</search-header>
		
		<div class="search-container">
			<div class="recent-search">
				<div class="title">
					<span>最近搜索</span>
					<img class="del-icon" src="../../assets/common_btn_del@2x.png" @click="delClick"/>
				</div>
				<div class="recent-list list-wrap">
					<ul class="clearfix">
						<li>婚礼</li>
						<li>家具/饰品</li>
						<li>创意/小礼物</li>
						<li>饰品</li>
						<li>手机壳</li>
					</ul>
				</div>
			</div>
			
			<div class="hot-search">
				<div class="title">
					<span>热搜</span>
				</div>
				<div class="hot-list list-wrap">
					<ul class="clearfix">
						<li>婚礼</li>
						<li>家具/饰品</li>
						<li>手机壳</li>
						<li>手机壳</li>
						<li>手机壳</li>
					</ul>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
import SearchHeader from '@/components/Header/SearchHeader'
	
export default {
	data(){
		return {
			
		}
	},
  components: {
  	SearchHeader
  },
  methods:{
  	searchClick(){
  		console.log('搜索')
  	},
  	delClick(){
  		this.$popup({
  			title:'确认删除全部历史记录?',
  			content:'',
  		}).then((res)=>{
  			console.log(res)
  		})
  	},
  },
}
</script>

<style lang="scss">
	@import "../../sass/baseSass.scss";
	#search{
		.search-btn{
			display: inline-block;
			font-size: 0.4rem;
			border-radius: 0.4rem;
			width: 1.33rem;
			height: 0.8rem;
			line-height: 0.8rem;
			text-align: center;
			background: #fff;
			color: #de7fef;
			margin-left: 0.27rem;
		}
		.search-container{
			padding: 0.4rem 0;
			.recent-search,
			.hot-search{
			}
			.title{
				font-size: 0.4rem;
				color: #333;
				padding:0 0.4rem; 

				.del-icon{
					float: right;
					width: 0.59rem;
					height: 0.59rem;
				}
			}
			.list-wrap{
				ul{
					margin: 0.4rem 0 0.67rem;
					padding-left: 0.4rem; 
					>li{
						float: left;
						font-size: 0.35rem;
						height: 0.8rem;
						line-height: 0.8rem;
						min-width: 1.6rem;
						border-radius: 0.4rem;
						padding: 0 0.4rem;
						margin-right: 0.27rem;
						margin-bottom: 0.27rem;
						text-align: center;
						color: #333;
						background: #f4f4f4;

					}
				}
			}
		}
	}
</style>


